@import '~assets/styles/h3Imports';

.root {
  column-gap: var(--spacing-xs, em(16));
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: var(--spacing-xs, em(16));
  row-gap: var(--spacing-xs, em(16));

  @media (max-width: $max-tablet-size) {
    grid-template-columns: 1fr;
    padding-top: var(--spacing-xxs, em(12));
  }
}
