@import '~assets/styles/h3Imports';

.root {
  --wellness-check-item-border-color: var(--gray-1, #d9dada);
  --wellness-check-item-background-color: var(--white, #ffffff);

  align-items: center;
  border: 1px solid var(--wellness-check-item-border-color);
  border-radius: em(8);
  display: flex;
  height: em(72);
  padding: var(--spacing-xs, rem(16));

  @media (max-width: #{$max-phone-size}) {
    background-color: var(--wellness-check-item-background-color);
    border: 0;
    box-shadow: var(--box-shadow);
    width: 100%;

    [data-input-control-element='label'] {
      margin-left: var(--spacing-xxxs, 0.5rem);
    }
  }
}

.care-gaps-checkbox {
  display: flex;
  width: 100%;
}
